<template>
  <div class="is-relative" style="height: 100%">
    <GnUI style="height: 100%" uiType="splitter" :options="splitterOptions">
      <div id="listMenu">
        <ul class="gn-list">
          <li
            v-for="(item, key) in confTypes"
            :key="key"
            :class="{ 'has-color-primary': isActive(item) }"
            @click="setActive(item)"
          >
            <a :class="{ 'has-text-light': isActive(item) }">
              <span>{{ item.name }}</span>
              <span v-if="item.id !== null">({{ item.id }})</span>
            </a>
          </li>
        </ul>
      </div>
      <!-- render view -->
      <div id="renderView">
        <!-- confRenderContainer -->
        <div id="confRenderContainer" ref="confRenderContainer">
          <ConfSetting v-if="currentType" :types="confTypes" :type="currentType" />
        </div>
      </div>
    </GnUI>
  </div>
</template>

<script>
  import GnUI from '@/components/core/GnUI'
  import { mapGetters, mapActions } from 'vuex'
  import GnConfEngine from '../../../dist/js/gnConfEngine.esm'
  import ConfSetting from './ConfSetting'
  import store from '@/store'

  const gnConfEngineTypes = [
    {
      name: 'COMMON',
      id: null
    }
  ]
  for (const key in GnConfEngine.types) {
    gnConfEngineTypes.push({
      id: GnConfEngine.types[key],
      name: key
    })
  }

  export default {
    name: 'ConfTypes',
    components: {
      GnUI,
      ConfSetting
    },
    data() {
      return {
        confTypes: gnConfEngineTypes,
        selectedType: null
      }
    },
    props: {
      typeId: {
        type: [Number, String]
      }
    },
    computed: {
      ...mapGetters(['confCategories', 'activeMenu']),
      splitterOptions() {
        return {
          panel: ['#listMenu', '#renderView'],
          space: 10,
          position: 350,
          orientation: 'vertical'
        }
      },
      currentType() {
        return this.typeId !== undefined && this.typeId !== null
          ? this.confTypes.find(t => t.id === +this.typeId)
          : gnConfEngineTypes[0]
      }
    },
    methods: {
      ...mapActions(['setPage']),
      isActive(confType) {
        return this.currentType && this.currentType.id === confType.id
      },
      setActive(confType) {
        this.$router.push({
          name: 'confType',
          params: {
            typeId: confType.id
          }
        },()=>{})
      }
    },
    mounted() {
      store.dispatch('setPage', 'confTypes')
    }
  }
</script>

<style lang="scss"></style>
