<template>
  <div class="is-relative" style="height: 100%">
    <GnUI style="height: 100%" uiType="splitter" :options="splitterOptions">
      <div id="listMenu">
        <ul class="gn-list">
          <li
            v-for="(item, index) in confCategories"
            :key="index"
            v-bind:class="{ 'has-color-primary': isActive(item.value) }"
          >
            <a v-on:click="setActive(item.value)">
              <span v-bind:class="{ 'has-text-light': isActive(item.value) }">{{
                item.label
              }}</span>
            </a>
          </li>
        </ul>
      </div>
      <!-- render view -->
      <div id="renderView">
        <!-- toolbar -->
        <div class="gn-container is-max conf-engine-toolbar">
          <button
            type="button"
            class="gn-button"
            v-on:click="validate()"
            :disabled="changedConf === false"
          >
            확인
          </button>
        </div>
        <!-- confRenderContainer -->
        <div id="confRenderContainer" ref="confRenderContainer"></div>
      </div>
    </GnUI>
  </div>
</template>

<script>
  import GnUI from '@/components/core/GnUI'
  import { mapGetters } from 'vuex'
  import store from '@/store'
  import GnConfEngine from '../../../dist/js/gnConfEngine.esm'

  let gnConfEngineInstance

  export default {
    name: 'ConfEngine',
    components: {
      GnUI
    },
    data() {
      return {
        changedConf: false
      }
    },
    computed: {
      ...mapGetters(['confCategories', 'activeMenu']),
      splitterOptions() {
        return {
          panel: ['#listMenu', '#renderView'],
          space: 10,
          position: 200,
          orientation: 'vertical'
        }
      }
    },
    methods: {
      isActive: menuItem => {
        return store.getters.activeMenu === menuItem
      },
      setActive(menuItem) {
        store.dispatch('activeMenu', menuItem)
        this.$refs.confRenderContainer.innerHTML = ''
        this.initConfList(this.$refs.confRenderContainer, menuItem)
      },
      validate: () => {
        if (gnConfEngineInstance && typeof gnConfEngineInstance.validate === 'function') {
          // eslint-disable-next-line no-console
          console.log('validate:', gnConfEngineInstance.validate())
        }
      },
      initConfList(target, category) {
        let that = this
        that.changedConf = false
        gnConfEngineInstance = GnConfEngine.create(target, category, {
          events: {
            SessionTimeout: {
              change: event => {
                // eslint-disable-next-line no-console
                console.log('change event!', event)
              }
            }
          },
          change: changeList => {
            // eslint-disable-next-line no-console
            console.log(changeList)
            that.changedConf = true
          }
        })
      }
    },
    created() {
      store.dispatch('activeMenu', store.getters.confCategories[0].value)
    },
    mounted() {
      this.initConfList(this.$refs.confRenderContainer, this.$store.getters.confCategories[0].value)
    },
    updated() {}
  }
</script>

<style lang="scss">
  .conf-engine-toolbar {
    border-bottom: 1px solid #dbdbdb;
  }
</style>
