<template>
  <span :class="['json-viewer', depth === 0 ? 'is-root' : '']">
    <template v-if="gnutil.isArray(jsonData) || gnutil.isObject(jsonData)">
      <ul>
        <li v-if="gnutil.isArray(jsonData)">[</li>
        <li v-if="gnutil.isPlainObject(jsonData)">{</li>
        <li v-for="(value, index) in jsonData" :key="index">
          <!-- key 값  -->
          <span class="json-label" :data-label="index" v-if="gnutil.isPlainObject(jsonData)">
            {{ index }} :
          </span>
          <!-- // key 값  -->
          <srcViewer :data="value" :parentData="jsonData" :depth="childDept" />,
        </li>
        <li v-if="gnutil.isArray(jsonData)">]</li>
        <li v-if="gnutil.isPlainObject(jsonData)"></li>
      </ul>
    </template>
    <span
      :data-path="parentPath"
      :data-label="jsonData"
      v-else-if="/^(http|https):\/\/[^\s]+$/.test(jsonData)"
    >
      <a :href="jsonData" target="_blank">{{ jsonData }}</a>
    </span>
    <span v-else :data-path="parentPath" :data-label="jsonData" :class="['json-value', typeString]">
      {{ jsonData }}
    </span>
  </span>
</template>

<script>
  import Gn from 'gnui'

  export default {
    name: 'srcViewer',
    props: {
      data: {
        type: [String, Number, Boolean, Array, Object],
        default: null
      },
      parentData: {
        type: [String, Number, Boolean, Array, Object],
        default: null
      },
      parentPath: {
        type: String,
        default: ''
      },
      parentComp: {},
      depth: {
        type: Number,
        default: 0
      }
    },
    data() {
      return {
        currentPath: '',
        gnutil: Gn.util
      }
    },
    computed: {
      typeString() {
        return this.data === null ? 'null' : typeof this.data
      },
      childDept() {
        return this.depth + 1
      },
      jsonData() {
        return this.isJson && this.gnutil.isString(this.data) ? JSON.parse(this.data) : this.data
      },
      isJson() {
        try {
          return this.gnutil.isObject(this.data) || typeof JSON.parse(this.data) === 'object'
        } catch {
          return false
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  .json-viewer {
    &.is-root {
      position: relative;
      display: block;
    }
    ul {
      margin: 0 0 0 0.2em;
      padding: 0 0 0 1em;
      list-style: none;
      li {
        padding: 0;
        margin: 0;
      }
      span.string,
      span.string a {
        color: rgb(91, 173, 95);
      }
      span.string::before,
      span.string::after {
        content: '"';
      }
      span.number {
        color: rgb(57, 105, 235);
      }
      span.null {
        color: rgb(255, 56, 56);
        &::before {
          content: 'null';
          font-style: italic;
        }
      }
      span.boolean {
        color: rgb(243, 98, 62);
      }
      span a {
        color: rgb(162, 162, 247);
        text-decoration: underline;
      }
    }
    .json-label {
      padding-left: 1rem;
    }
  }
</style>
