<template>
  <div>
    <h4 class="gn-title is-size4">{{ confType }}{{ typeNumber }}</h4>
    <div class="gn-box2">
      <h6 class="gn-title is-size6" v-show="typeOptions.name">{{ typeOptions.name }}</h6>
      <p v-show="typeOptions.description" v-html="typeOptions.description"></p>
    </div>
    <h6 class="gn-title is-size6" v-show="hasSample">사용 예제</h6>
    <div class="gn-box gn-grid" v-show="hasSample">
      <div class="gn-column is-8" ref="example"></div>
      <div class="gn-column is-4 gn-box viewer">
        <srcViewer v-if="hasSample" :data="typeOptions.confList" />
      </div>
    </div>
    <div class="conf-type-guide">
      <GnUI uiType="jsonview" :options="viewOption" ref="optionView" />
    </div>
  </div>
</template>

<script>
  import GnUI from '@/components/core/GnUI'
  import srcViewer from './components/srcViewer'
  import docs from '@/assets/types'
  import GnConfEngine from '../../../dist/js/gnConfEngine.esm'

  export default {
    name: 'ConfSetting',
    props: {
      types: Array,
      type: Object
    },
    data() {
      return {}
    },
    computed: {
      confType() {
        // 현재 활성화 된 메뉴를 타입으로 사용
        return this.type.name
      },
      typeNumber() {
        // 해당 타입의 CONF_TYPE
        return this.type.id ? `(${this.type.id})` : ''
      },
      typeOptions() {
        // return  { name: '작업예정' }
        return docs[this.confType.toLowerCase()] || { name: '작업예정' }
      },
      hasSample() {
        return !!this.typeOptions.confList
      },
      viewOption() {
        return {
          schema: this.typeOptions.schema
            ? this.typeOptions.schema
            : {
                Type: 'array',
                Items: {
                  Type: 'object',
                  Properties: {
                    field: {
                      Type: 'string',
                      Disp: '필드명'
                    },
                    role: {
                      Type: 'string',
                      Disp: '역할'
                    },
                    description: {
                      Type: ['array', 'html'],
                      Disp: '설명',
                      Items: {
                        Type: 'object',
                        Properties: {
                          field: {
                            Type: 'string',
                            Disp: null
                          },
                          role: {
                            Type: 'string',
                            Disp: null
                          },
                          description: {
                            Type: 'html',
                            Disp: null
                          }
                        }
                      }
                    }
                  }
                }
              },
          data: this.typeOptions.fields ? this.typeOptions.fields : []
        }
      }
    },
    watch: {
      type() {
        this.$refs.optionView.comp && this.$refs.optionView.comp.reRender(this.viewOption)
        // example
        if (this.hasSample) {
          this.$refs.example.innerHTML = ''
          this.confEngine = GnConfEngine.render(this.$refs.example, {
            confList: this.typeOptions.confList
          })
        }
      }
    },
    components: {
      GnUI,
      srcViewer
    }
  }
</script>

