    tr:last-child {
      border-bottom-width: 0;
      td {
        padding-bottom: 0;
      }
    }
  }
  .jsonview-contents .gn-table.is-borderless > tr > th,
  .jsonview-contents .gn-table.is-borderless > tr > td {
    display: block;
  }
  .jsonview-contents .gn-table.is-borderless > tr > th {
    text-align: left;
    padding-top: 30px;
  }
